import { Avatar, Box, Card, Stack, Typography, useTheme } from '@mui/material'
import { Fragment } from 'react'
import { ADMIN_EMAIL } from '../App'
import { useAppSelector } from '../states/hooks'
import DashBoard from './admin/dashboard'

const employeeMenu = [
  'ค้นหาโครงการ การคัดเลือก',
  'รายการโครงการ การคัดเลือก',
  'Air Plan'
]
const employerMenu = [
  'เพิ่มโครงการ การคัดเลือก',
  'รายการโครงการ การคัดเลือก',
  'Air Plan'
]

export default function Account() {
  const { fbUser, user } = useAppSelector((state) => state.auth)

  const email = localStorage.getItem('email')
  const theme = useTheme()

  if (fbUser?.email == ADMIN_EMAIL) {
    return <DashBoard />
  }

  const cardMenu = ({
    title,
    // url,
    onClick
  }: {
    title: string
    // url: string
    onClick?: () => void
  }) => {
    return (
      <Card
        elevation={4}
        style={{
          width: '450px',
          cursor: 'pointer',
          background:
            'linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,121,39,1) 0%, rgba(0,212,255,1) 100%)'
        }}
        onClick={onClick}
      >
        <Box
          display="flex"
          alignItems="center"
          justifyContent="center"
          width="450px"
          pl={2}
          // style={{
          //   backgroundImage: `url(${url})`,
          //   backgroundRepeat: 'no-repeat'
          // }}
          height="70px"
        >
          <Typography
            variant="h5"
            component="p"
            color="white"
            style={{ textShadow: '3px 3px 8px #000' }}
          >
            {title}
          </Typography>
        </Box>
      </Card>
    )
  }

  const personCard = () => {
    return (
      <Card elevation={4}>
        <Box
          style={{ backgroundColor: theme.palette.secondary.main }}
          display="flex"
          justifyContent="center"
        >
          <Typography variant="h6" color="white">
            ข้อมูลโปรไฟล์
          </Typography>
        </Box>
        <Box p={2} width="450px">
          <Stack direction="row" spacing={2}>
            <Avatar src="https://campus.campus-star.com/app/uploads/2019/02/N08-1-768x960.jpg">
              {(email ?? '-').charAt(0).toLocaleUpperCase()}
            </Avatar>
            <Typography variant="body1" color="inherit">
              {user?.fullName ?? ''}
            </Typography>
          </Stack>
          {user?.approve && (
            <Typography variant="h6" color="green">
              ยืนยันข้อมูลสำเร็จ
            </Typography>
          )}
          <Typography variant="body1" color="inherit">
            ประเภคผู้ใช้ : {user?.typeText ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            ประเภคบุคคล : {user?.personTypeText ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            Email : {fbUser?.email ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            บัตรประชาชน : {user?.personCardID ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            โทร : {user?.tel ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            วันเดือนปีเกิด : {user?.birthDay.toString() ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            ที่อยู่ : {user?.fullAddressText ?? ''}
          </Typography>
        </Box>
      </Card>
    )
  }

  const companyCard = () => {
    return (
      <Card elevation={4}>
        <Box
          style={{ backgroundColor: theme.palette.secondary.main }}
          display="flex"
          justifyContent="center"
        >
          <Typography variant="h6" color="white">
            ข้อมูลบริษัท
          </Typography>
        </Box>
        <Box p={2} width="450px">
          <Typography variant="body1" color="inherit">
            ชื่อ : {user?.company?.name ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            เลขประจำตัวผู้เสียภาษีอากร : {user?.company?.comID ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            ที่อยู่ : {user?.company?.fullAddressText ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            Email : {user?.company?.email ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            โทร : {user?.company?.tel ?? ''}
          </Typography>
          <Typography variant="body1" color="inherit">
            เว็บไซต์ : {user?.company?.web ?? ''}
          </Typography>
        </Box>
      </Card>
    )
  }

  return (
    <Box display="flex" justifyContent="center" pt={2} pb={16}>
      <Stack direction="row" spacing={2}>
        <Stack direction="column" spacing={2}>
          {user?.type == 'employee' &&
            employeeMenu.map((e, i) => (
              <Fragment key={i}>
                {cardMenu({
                  title: e
                  // url: '/images/p2.jpg'
                })}
              </Fragment>
            ))}
          {user?.type == 'employer' &&
            employerMenu.map((e, i) => (
              <Fragment key={i}>
                {cardMenu({
                  title: e
                  // url: '/images/p2.jpg'
                })}
              </Fragment>
            ))}
        </Stack>
        <Stack direction="column" spacing={2}>
          {personCard()}
          {user?.personType === 'company' && companyCard()}
        </Stack>
      </Stack>
    </Box>
  )
}
